.schedule-webinar-card {

  max-width: 622px;
  width: 100%;

  & + & {
    margin-top: 16px;
  }

  &--past {
    opacity: 0.5;
  }

  &__wrapper {
    display: flex;
    gap: 40px;
    padding: 32px 40px 32px 32px;
    background: $white;
    border-radius: 8px 24px 24px 8px;
    border-left: 8px solid $primary;
    filter: drop-shadow(0px 4px 30px rgba(80, 106, 153, 0.1));
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 8px;

    &-item {
      font-weight: 400;
      font-size: 16px;
      line-height: 120%;
      color: #3F5770;
      display: flex;
      gap: 8px;
      align-items: center;
    }
  }

  &__speaker {
    align-items: flex-start;
  }

  &__date {
    font-weight: 600;
    font-size: 16px;
    line-height: 140%;
    color: #3F5770;
  }

  &__link {
    margin-top: auto;
    font-weight: 600;
    font-size: 16px;
    line-height: 140%;
    color: $primary;
    text-decoration: none;
  }

  &__details {
    max-width: 360px;
    width: 100%;
  }

  &__title {
    font-weight: 600;
    font-size: 16px;
    line-height: 140%;
    color: #3F5770;
    margin-bottom: 16px;
  }

  &__text {}
}
